




















import { Component, Mixins, Prop } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";
import { DictionaryItem } from "@/mode/dictionary-item";
@Component({
    name: "MySelect"
})
export default class MySelect extends Mixins(TwoWay) {
    @Prop({ default: "edit" })
    mode!: string;

    @Prop()
    options!: DictionaryItem[];

    get isMul(): boolean {
        return typeof this.currentValue === "object" && Array.isArray(this.currentValue);
    }
    get currentItem(): DictionaryItem | DictionaryItem[] | undefined {
        let currentItem;
        if (this.isMul) {
            currentItem = this.options.filter(item => this.currentValue.includes(item.value));
        } else {
            currentItem = this.options.find((item: any) => item.value == this.currentValue);
        }
        return currentItem;
    }
}
